.container {
  max-width: 1200px;
  margin: 0 auto;
}
.text-white {
  color: $white;
}

@each $name,$value in $spacers {
  .mt-#{$name} {
    margin-top: $value;
  }
  .mb-#{$name} {
    margin-bottom: $value;
  }
  .mr-#{$name} {
    margin-right: $value;
  }
  .ml-#{$name} {
    margin-left: $value;
  }
}